export function getRoomCode() {
    let roomCode
    let codeValid = true
    while (codeValid) {
        roomCode = prompt('Room code') || ''

        if (roomCode.length === 4 ) {
            codeValid = false
            localStorage.removeItem('RoomCode');
            localStorage.setItem('RoomCode', roomCode);
            alert('🟢 Сode set')
            return roomCode
        } else {
            alert('🔴 Сode must be a 4 numbers')
        }
    }
}
